import React from "react";
import { v4 as uuidv4 } from "uuid";
import { DeviceUUID } from "device-uuid";
class App extends React.Component {
  constructor(props) {
    super(props);
    var du = new DeviceUUID().parse();
    console.log(du);
    this.state = {
      uuid: new DeviceUUID().get(),
      du: du,
    };
  }

  componentDidMount = () => {
    var uuid = uuidv4();
    if (localStorage.getItem("uuid") != undefined) {
      this.setState({
        uuid: this.getCookie("uuid"),
      });
    } else {
      this.setState(
        {
          uuid,
        },
        () => {
          document.cookie = "uuid=" + uuid + "; expires=Thu, 18 Dec 2033 12:00:00 UTC";
          localStorage.setItem("uuid", uuid);
        }
      );
    }

    console.log(this.getCookie("uuid"));
  };
  getCookie = (name) => {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
  };
  render() {
    return (
      <div>
        <div style={{ width: "100%", display: "flex", justifyContent: "center", marginTop: 50 }}>localstorage : {this.state.uuid}</div>
        <p style={{ width: "100%", marginTop: 50, textAlign: "center" }}>{this.state.du.source}</p>
      </div>
    );
  }
}

export default App;
